<div class="hello"><h1 class="ui header"><img src="../assets/handshake1.webp">自學2.0使用說明<img src="../assets/handshake1.webp"></h1><div class="ui grid container"><div class="ui two column stackable row"><div class="ui padded segment column"><h2 class="ui header">自學2.0是什麼？</h2><p> <b>自學2.0</b>是一個公益尋友平台，旨在透過地理位置、個人興趣及專長來協助用戶尋找朋友。平台的核心功能「互助旗」提供公開的自我介紹，讓人們在見面前能初步了解和認識對方。</p><h2 class="ui header">自學2.0由什麼單位在設計及維護？</h2><p>本平台由非營利組織<a href="http://www.alearn.org.tw" target="_blank">中華民國自主學習促進會</a>設計及維護，致力於提供一個安全且友善的環境，讓大家能夠自由地尋找學習夥伴。</p><h2 class="ui header">互助旗是什麼？</h2><p> <b>互助旗</b>是一個帶有概略地理位置的自我介紹功能，讓人在與您見面之前，便能對您的興趣、專長及背景有初步的認識，從而更容易找到志同道合的朋友。</p><h2 class="ui header">互助旗需更新嗎？</h2><p>為了讓有需要的朋友能夠順利找到您，請至少每年更新一次您的互助旗。系統會自動隱藏一年以上未更新的互助旗，而對於兩年以上未更新的互助旗，系統則會進行刪除處理，以確保信息的時效性和準確性。</p></div><div class="ui padded segment column"><h2 class="ui header">我的互助旗會是公開的嗎？能不能保密？</h2><p>互助旗是完全公開的，您可以選擇只留下您願意公開的資訊。請參考我們的<router-link to="privacy-policy">隱私權政策</router-link>，以了解如何更好地保護您的隱私。</p><h2 class="ui header">升起互助旗後可以修改嗎？</h2><p>當然可以！只需再次登入平台，點選「我的旗幟」，即可隨時修改您的互助旗內容，保持信息的最新和準確。</p><h2 class="ui header">升起互助旗後可以移除嗎？</h2><p>可以，您只需發送e-mail至管理員的郵箱(<a href="mailto:bestian@gmail.com">bestian@gmail.com</a>)請求手動移除您的互助旗，我們會盡快為您處理。</p><h2 class="ui header">地圖介面有什麼用意？</h2><p>地圖介面讓您能夠根據地理位置就近尋找夥伴。例如，您可以查看「宜蘭附近有沒有自學朋友呢？」透過地圖，您可以一目了然地找到附近的學習夥伴，增進彼此的互動和交流。</p></div></div></div></div>